
<template>
	<div class="app-contain" :style='{"minHeight":"100vh","padding":"0","margin":"20px auto 60px","borderRadius":"0px","background":"#fff","width":"64%","position":"relative","height":"100%"}'>
		<div class="bread_view">
			<el-breadcrumb separator="/" class="breadcrumb">
				<el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="12">
					<el-form-item label="测试名称" prop="ceshimingcheng">
						<el-input class="list_inp" v-model="form.ceshimingcheng" placeholder="测试名称"
							 type="text" 							:readonly="!isAdd||disabledForm.ceshimingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="问题一" prop="wentiyi">
						<el-input class="list_inp" v-model="form.wentiyi" placeholder="问题一"
							 type="text" 							:readonly="!isAdd||disabledForm.wentiyi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="回答一" prop="huidayi">
						<el-input class="list_inp" v-model="form.huidayi" placeholder="回答一"
							 type="text" 							:readonly="!isAdd||disabledForm.huidayi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="问题二" prop="wentier">
						<el-input class="list_inp" v-model="form.wentier" placeholder="问题二"
							 type="text" 							:readonly="!isAdd||disabledForm.wentier?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="回答二" prop="huidaer">
						<el-input class="list_inp" v-model="form.huidaer" placeholder="回答二"
							 type="text" 							:readonly="!isAdd||disabledForm.huidaer?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="问题三" prop="wentisan">
						<el-input class="list_inp" v-model="form.wentisan" placeholder="问题三"
							 type="text" 							:readonly="!isAdd||disabledForm.wentisan?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="回答三" prop="huidasan">
						<el-input class="list_inp" v-model="form.huidasan" placeholder="回答三"
							 type="text" 							:readonly="!isAdd||disabledForm.huidasan?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="问题四" prop="wentisi">
						<el-input class="list_inp" v-model="form.wentisi" placeholder="问题四"
							 type="text" 							:readonly="!isAdd||disabledForm.wentisi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="回答四" prop="huidasi">
						<el-input class="list_inp" v-model="form.huidasi" placeholder="回答四"
							 type="text" 							:readonly="!isAdd||disabledForm.huidasi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="问题五" prop="wentiwu">
						<el-input class="list_inp" v-model="form.wentiwu" placeholder="问题五"
							 type="text" 							:readonly="!isAdd||disabledForm.wentiwu?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="回答五" prop="huidawu">
						<el-input class="list_inp" v-model="form.huidawu" placeholder="回答五"
							 type="text" 							:readonly="!isAdd||disabledForm.huidawu?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="学号" prop="xuehao">
						<el-input class="list_inp" v-model="form.xuehao" placeholder="学号"
							 type="text" 							:readonly="!isAdd||disabledForm.xuehao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="学生姓名" prop="xueshengxingming">
						<el-input class="list_inp" v-model="form.xueshengxingming" placeholder="学生姓名"
							 type="text" 							:readonly="!isAdd||disabledForm.xueshengxingming?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="测试时间" prop="ceshishijian">
						<el-date-picker
							class="list_date"
							v-model="form.ceshishijian"
							format="YYYY-MM-DD HH:mm:ss"
							value-format="YYYY-MM-DD HH:mm:ss"
							type="datetime"
							style="width:100%;"
							:readonly="!isAdd||disabledForm.ceshishijian?true:false"
							placeholder="请选择测试时间" />
					</el-form-item>
				</el-col>
			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick">取消</el-button>
				<el-button class="formModel_confirm" @click="save"
					type="success"
					>
					保存
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//基础信息
	const tableName = 'ceshihuida'
	const formName = '测试回答'
	//基础信息
	const breadList = ref([{
		name: formName
	}])
	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form表单
	const form = ref({
		ceshimingcheng: '',
		wentiyi: '',
		huidayi: '',
		wentier: '',
		huidaer: '',
		wentisan: '',
		huidasan: '',
		wentisi: '',
		huidasi: '',
		wentiwu: '',
		huidawu: '',
		xuehao: '',
		xueshengxingming: '',
		ceshishijian: '',
		crossuserid: '',
		crossrefid: '',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		ceshimingcheng : false,
		wentiyi : false,
		huidayi : false,
		wentier : false,
		huidaer : false,
		wentisan : false,
		huidasan : false,
		wentisi : false,
		huidasi : false,
		wentiwu : false,
		huidawu : false,
		xuehao : false,
		xueshengxingming : false,
		ceshishijian : false,
		crossuserid : false,
		crossrefid : false,
	})
	const isAdd = ref(false)
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		ceshimingcheng: [
		],
		wentiyi: [
		],
		huidayi: [
		],
		wentier: [
		],
		huidaer: [
		],
		wentisan: [
		],
		huidasan: [
		],
		wentisi: [
		],
		huidasi: [
		],
		wentiwu: [
		],
		huidawu: [
		],
		xuehao: [
		],
		xueshengxingming: [
		],
		ceshishijian: [
		],
		crossuserid: [
		],
		crossrefid: [
		],
	})
	//methods

	//methods
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
			form.value.ceshishijian = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='ceshimingcheng'){
					form.value.ceshimingcheng = row[x];
					disabledForm.value.ceshimingcheng = true;
					continue;
				}
				if(x=='wentiyi'){
					form.value.wentiyi = row[x];
					disabledForm.value.wentiyi = true;
					continue;
				}
				if(x=='huidayi'){
					form.value.huidayi = row[x];
					disabledForm.value.huidayi = true;
					continue;
				}
				if(x=='wentier'){
					form.value.wentier = row[x];
					disabledForm.value.wentier = true;
					continue;
				}
				if(x=='huidaer'){
					form.value.huidaer = row[x];
					disabledForm.value.huidaer = true;
					continue;
				}
				if(x=='wentisan'){
					form.value.wentisan = row[x];
					disabledForm.value.wentisan = true;
					continue;
				}
				if(x=='huidasan'){
					form.value.huidasan = row[x];
					disabledForm.value.huidasan = true;
					continue;
				}
				if(x=='wentisi'){
					form.value.wentisi = row[x];
					disabledForm.value.wentisi = true;
					continue;
				}
				if(x=='huidasi'){
					form.value.huidasi = row[x];
					disabledForm.value.huidasi = true;
					continue;
				}
				if(x=='wentiwu'){
					form.value.wentiwu = row[x];
					disabledForm.value.wentiwu = true;
					continue;
				}
				if(x=='huidawu'){
					form.value.huidawu = row[x];
					disabledForm.value.huidawu = true;
					continue;
				}
				if(x=='xuehao'){
					form.value.xuehao = row[x];
					disabledForm.value.xuehao = true;
					continue;
				}
				if(x=='xueshengxingming'){
					form.value.xueshengxingming = row[x];
					disabledForm.value.xueshengxingming = true;
					continue;
				}
				if(x=='ceshishijian'){
					form.value.ceshishijian = row[x];
					disabledForm.value.ceshishijian = true;
					continue;
				}
				if(x=='crossuserid'){
					form.value.crossuserid = row[x];
					disabledForm.value.crossuserid = true;
					continue;
				}
				if(x=='crossrefid'){
					form.value.crossrefid = row[x];
					disabledForm.value.crossrefid = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('xuehao') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.xuehao = json.xuehao
				disabledForm.value.xuehao = true;
			}
			if(json.hasOwnProperty('xueshengxingming') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.xueshengxingming = json.xueshengxingming
				disabledForm.value.xueshengxingming = true;
			}
		})
	}
	//初始化
	//取消
	const backClick = () => {
		history.back()
	}
	//提交
	const save=()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									history.back()
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							history.back()
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
	
</script>
<style lang="scss" scoped>
	// 面包屑盒子
	.bread_view {
		border-radius: 0px;
		padding: 12px 20px;
		margin: 0px auto;
		background: none;
		width: 100%;
		border-color: #eee;
		border-width: 0 0 0px;
		position: relative;
		border-style: solid;
		:deep(.breadcrumb) {
			font-size: 14px;
			line-height: 1;
			.el-breadcrumb__separator {
				margin: 0 9px;
				color: #999;
				font-weight: 500;
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
					color: #333;
					display: inline-block;
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
					color: #999;
					display: inline-block;
				}
			}
		}
	}
	// 表单
	.add_form{
		border: 0px solid #2da065;
		border-radius: 0px;
		padding: 30px 7%;
		background: none;
		// form item
		:deep(.el-form-item) {
			border: 0px solid #eee;
			padding: 6px 0;
			margin: 0 0 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 background: none;
			 display: block;
			 width: auto;
			 min-width: 150px;
			 text-align: right;
			}
			// 内容盒子
			.el-form-item__content {
				display: flex;
				width: calc(100% - 150px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 输入框
				.list_inp {
					padding: 0 10px;
					background: none;
					width: auto;
					border-color: #ccc;
					border-width: 1px;
					line-height: 36px;
					box-sizing: border-box;
					border-style: solid;
					min-width: 100%;
					height: 36px;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				//日期选择器
				.list_date {
					border-radius: 0px;
					background: none;
					width: auto;
					border-color: #ccc;
					border-width: 1px;
					line-height: 36px;
					box-sizing: border-box;
					border-style: solid;
					min-width: 100%;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
					}
				}
			}
		}
	}
	// 按钮盒子
	.formModel_btn_box {
		padding: 0 150px;
		display: flex;
		width: 100%;
		justify-content: flex-start;
		align-items: center;
		.formModel_cancel {
			border: 0px solid #27bacc;
			cursor: pointer;
			border-radius: 0px;
			padding: 0 40px;
			margin: 0 20px 0 0;
			color: #fff;
			background: #999;
			width: auto;
			font-size: 14px;
			line-height: 40px;
			transition: all 0.3s;
			height: 40px;
		}
		.formModel_cancel:hover {
		}
		
		.formModel_confirm {
			border: 0;
			cursor: pointer;
			border-radius: 0px;
			padding: 0 40px;
			margin: 0 20px 0 0;
			color: #fff;
			background: #113961;
			width: auto;
			font-size: 14px;
			line-height: 40px;
			transition: all 0.3s;
			height: 40px;
		}
		.formModel_confirm:hover {
		}
	}
</style>