import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import forumList from '@/views/pages/forum/list'
import xueshengList from '@/views/pages/xuesheng/list'
import xueshengDetail from '@/views/pages/xuesheng/formModel'
import xueshengAdd from '@/views/pages/xuesheng/formAdd'
import xueshengRegister from '@/views/pages/xuesheng/register'
import xueshengCenter from '@/views/pages/xuesheng/center'
import jiaoshiList from '@/views/pages/jiaoshi/list'
import jiaoshiDetail from '@/views/pages/jiaoshi/formModel'
import jiaoshiAdd from '@/views/pages/jiaoshi/formAdd'
import xinliwenzhangList from '@/views/pages/xinliwenzhang/list'
import xinliwenzhangDetail from '@/views/pages/xinliwenzhang/formModel'
import xinliwenzhangAdd from '@/views/pages/xinliwenzhang/formAdd'
import storeupList from '@/views/pages/storeup/list'
import xinlijiaoshiList from '@/views/pages/xinlijiaoshi/list'
import xinlijiaoshiDetail from '@/views/pages/xinlijiaoshi/formModel'
import xinlijiaoshiAdd from '@/views/pages/xinlijiaoshi/formAdd'
import zixunyuyueList from '@/views/pages/zixunyuyue/list'
import zixunyuyueDetail from '@/views/pages/zixunyuyue/formModel'
import zixunyuyueAdd from '@/views/pages/zixunyuyue/formAdd'
import quxiaoyuyueList from '@/views/pages/quxiaoyuyue/list'
import quxiaoyuyueDetail from '@/views/pages/quxiaoyuyue/formModel'
import quxiaoyuyueAdd from '@/views/pages/quxiaoyuyue/formAdd'
import xianshangzixunList from '@/views/pages/xianshangzixun/list'
import xianshangzixunDetail from '@/views/pages/xianshangzixun/formModel'
import xianshangzixunAdd from '@/views/pages/xianshangzixun/formAdd'
import xinlipingjiaList from '@/views/pages/xinlipingjia/list'
import xinlipingjiaDetail from '@/views/pages/xinlipingjia/formModel'
import xinlipingjiaAdd from '@/views/pages/xinlipingjia/formAdd'
import xinliceshiList from '@/views/pages/xinliceshi/list'
import xinliceshiDetail from '@/views/pages/xinliceshi/formModel'
import xinliceshiAdd from '@/views/pages/xinliceshi/formAdd'
import ceshihuidaList from '@/views/pages/ceshihuida/list'
import ceshihuidaDetail from '@/views/pages/ceshihuida/formModel'
import ceshihuidaAdd from '@/views/pages/ceshihuida/formAdd'
import xinlicepingbaogaoList from '@/views/pages/xinlicepingbaogao/list'
import xinlicepingbaogaoDetail from '@/views/pages/xinlicepingbaogao/formModel'
import xinlicepingbaogaoAdd from '@/views/pages/xinlicepingbaogao/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'forumList',
			component: forumList
		}
		, {
			path: 'xueshengList',
			component: xueshengList
		}, {
			path: 'xueshengDetail',
			component: xueshengDetail
		}, {
			path: 'xueshengAdd',
			component: xueshengAdd
		}
		, {
			path: 'xueshengCenter',
			component: xueshengCenter
		}
		, {
			path: 'jiaoshiList',
			component: jiaoshiList
		}, {
			path: 'jiaoshiDetail',
			component: jiaoshiDetail
		}, {
			path: 'jiaoshiAdd',
			component: jiaoshiAdd
		}
		, {
			path: 'xinliwenzhangList',
			component: xinliwenzhangList
		}, {
			path: 'xinliwenzhangDetail',
			component: xinliwenzhangDetail
		}, {
			path: 'xinliwenzhangAdd',
			component: xinliwenzhangAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'xinlijiaoshiList',
			component: xinlijiaoshiList
		}, {
			path: 'xinlijiaoshiDetail',
			component: xinlijiaoshiDetail
		}, {
			path: 'xinlijiaoshiAdd',
			component: xinlijiaoshiAdd
		}
		, {
			path: 'zixunyuyueList',
			component: zixunyuyueList
		}, {
			path: 'zixunyuyueDetail',
			component: zixunyuyueDetail
		}, {
			path: 'zixunyuyueAdd',
			component: zixunyuyueAdd
		}
		, {
			path: 'quxiaoyuyueList',
			component: quxiaoyuyueList
		}, {
			path: 'quxiaoyuyueDetail',
			component: quxiaoyuyueDetail
		}, {
			path: 'quxiaoyuyueAdd',
			component: quxiaoyuyueAdd
		}
		, {
			path: 'xianshangzixunList',
			component: xianshangzixunList
		}, {
			path: 'xianshangzixunDetail',
			component: xianshangzixunDetail
		}, {
			path: 'xianshangzixunAdd',
			component: xianshangzixunAdd
		}
		, {
			path: 'xinlipingjiaList',
			component: xinlipingjiaList
		}, {
			path: 'xinlipingjiaDetail',
			component: xinlipingjiaDetail
		}, {
			path: 'xinlipingjiaAdd',
			component: xinlipingjiaAdd
		}
		, {
			path: 'xinliceshiList',
			component: xinliceshiList
		}, {
			path: 'xinliceshiDetail',
			component: xinliceshiDetail
		}, {
			path: 'xinliceshiAdd',
			component: xinliceshiAdd
		}
		, {
			path: 'ceshihuidaList',
			component: ceshihuidaList
		}, {
			path: 'ceshihuidaDetail',
			component: ceshihuidaDetail
		}, {
			path: 'ceshihuidaAdd',
			component: ceshihuidaAdd
		}
		, {
			path: 'xinlicepingbaogaoList',
			component: xinlicepingbaogaoList
		}, {
			path: 'xinlicepingbaogaoDetail',
			component: xinlicepingbaogaoDetail
		}, {
			path: 'xinlicepingbaogaoAdd',
			component: xinlicepingbaogaoAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/xueshengRegister',
		component: xueshengRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
